<template >
    <div class="container">
        <div class="product__card">
            <img :src="insideIce.img" alt="">
            <div class="product__info">
                <h2>{{ insideIce.title }}</h2>
                <p>{{ insideIce.text }}</p>
                <div class="product__item">
                    <a class="product__price">$19.55</a>
                    <a class="product__prices">$22.55</a>
                </div>
                <router-link to="/product"><a class="nav__order"> Buy Now</a></router-link>
            </div>
        </div>
    </div>
</template>
<script>
import bd from "@/bd";
export default {
    data() {
        return {
            ices: bd,
            insideId: null,
        };
    },

    created() {
        this.insideId = this.$route.params.id;
    },
    computed: {
        insideIce() {
            return this.ices.find((ice) => this.insideId == ice.id);
        },
    },
};
</script>
